<template>
  <div class="main-container">
    <div class="tool-header">
      <h1>üìä CSVÂ§ÑÁêÜÂô®</h1>
      <p>CSVÊñá‰ª∂ÁöÑËß£Êûê„ÄÅÁºñËæë„ÄÅËΩ¨Êç¢ÂíåÂØºÂá∫Â∑•ÂÖ∑</p>
    </div>

    <div class="toolbar">
      <el-upload
        action=""
        :before-upload="handleFileUpload"
        :show-file-list="false"
        accept=".csv,.txt"
        class="upload-demo"
      >
        <el-button>
          <i class="el-icon-upload2"></i> ‰∏ä‰º†CSVÊñá‰ª∂
        </el-button>
      </el-upload>

      <div class="toolbar-actions">
        <el-button @click="loadSample">
          <i class="el-icon-document"></i> Âä†ËΩΩÁ§∫‰æã
        </el-button>
        <el-button @click="clearData">
          <i class="el-icon-delete"></i> Ê∏ÖÁ©∫Êï∞ÊçÆ
        </el-button>
        <el-button @click="addRow">
          <i class="el-icon-plus"></i> Ê∑ªÂä†Ë°å
        </el-button>
        <el-button @click="addColumn">
          <i class="el-icon-plus"></i> Ê∑ªÂä†Âàó
        </el-button>
        <el-button @click="downloadCsv">
          <i class="el-icon-download"></i> ‰∏ãËΩΩCSV
        </el-button>
        <el-button @click="downloadJson">
          <i class="el-icon-download"></i> ‰∏ãËΩΩJSON
        </el-button>
      </div>
    </div>

    <el-row :gutter="20">
      <el-col :xs="24" :sm="24" :md="16" :lg="16" :xl="16">
        <el-card class="data-card">
          <div slot="header">
            <span>Êï∞ÊçÆË°®Ê†º</span>
            <div style="float: right;">
              <el-input
                v-model="searchText"
                placeholder="ÊêúÁ¥¢Êï∞ÊçÆ..."
                size="mini"
                style="width: 200px;"
                prefix-icon="el-icon-search"
                @input="handleSearch"
              ></el-input>
            </div>
          </div>
          
          <div class="table-container">
            <el-table
              :data="filteredData"
              border
              stripe
              height="400"
              @cell-click="handleCellClick"
              class="csv-table"
            >
              <el-table-column
                v-for="(column, index) in columns"
                :key="index"
                :prop="column.prop"
                :label="column.label"
                :width="column.width"
                show-overflow-tooltip
              >
                <template slot="header">
                  <el-input
                    v-if="editingHeader === index"
                    v-model="column.label"
                    size="mini"
                    @blur="editingHeader = -1"
                    @keyup.enter="editingHeader = -1"
                    ref="headerInput"
                  ></el-input>
                  <span v-else @dblclick="editHeader(index)">
                    {{ column.label }}
                    <el-button
                      size="mini"
                      type="text"
                      @click="deleteColumn(index)"
                    >
                      <i class="el-icon-close"></i>
                    </el-button>
                  </span>
                </template>
                
                <template slot-scope="scope">
                  <el-input
                    v-if="editingCell.row === scope.$index && editingCell.col === index"
                    v-model="scope.row[column.prop]"
                    size="mini"
                    @blur="editingCell = {}"
                    @keyup.enter="editingCell = {}"
                    ref="cellInput"
                  ></el-input>
                  <span v-else>{{ scope.row[column.prop] }}</span>
                </template>
              </el-table-column>
              
              <el-table-column label="Êìç‰Ωú" width="80" fixed="right">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    type="text"
                    @click="deleteRow(scope.$index)"
                  >
                    <i class="el-icon-delete"></i>
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
          
          <div class="table-stats">
            <span>ÊÄªË°åÊï∞: {{ csvData.length }}</span>
            <span>ÊÄªÂàóÊï∞: {{ columns.length }}</span>
            <span>ÊòæÁ§∫Ë°åÊï∞: {{ filteredData.length }}</span>
          </div>
        </el-card>
      </el-col>

      <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
        <el-card class="settings-card">
          <div slot="header">
            <span>CSVËÆæÁΩÆ</span>
          </div>
          
          <el-form label-width="80px">
            <el-form-item label="ÂàÜÈöîÁ¨¶">
              <el-select v-model="csvSettings.delimiter" @change="parseRawData">
                <el-option label="ÈÄóÂè∑ (,)" value=","></el-option>
                <el-option label="ÂàÜÂè∑ (;)" value=";"></el-option>
                <el-option label="Âà∂Ë°®Á¨¶" value="\t"></el-option>
                <el-option label="Á´ñÁ∫ø (|)" value="|"></el-option>
              </el-select>
            </el-form-item>
            
            <el-form-item label="ÂºïÂè∑">
              <el-select v-model="csvSettings.quote" @change="parseRawData">
                <el-option label="ÂèåÂºïÂè∑ (&quot;)" value="&quot;"></el-option>
                <el-option label="ÂçïÂºïÂè∑ (')" value="'"></el-option>
                <el-option label="Êó†" value=""></el-option>
              </el-select>
            </el-form-item>
            
            <el-form-item>
              <el-checkbox v-model="csvSettings.hasHeader" @change="parseRawData">
                ÂåÖÂê´Ê†áÈ¢òË°å
              </el-checkbox>
            </el-form-item>
            
            <el-form-item>
              <el-checkbox v-model="csvSettings.skipEmptyLines" @change="parseRawData">
                Ë∑≥ËøáÁ©∫Ë°å
              </el-checkbox>
            </el-form-item>
          </el-form>
        </el-card>

        <el-card class="raw-data-card" style="margin-top: 20px;">
          <div slot="header">
            <span>ÂéüÂßãÊï∞ÊçÆ</span>
          </div>
          
          <el-input
            type="textarea"
            v-model="rawData"
            placeholder="Á≤òË¥¥CSVÊï∞ÊçÆÊàñ‰∏ä‰º†Êñá‰ª∂..."
            :rows="10"
            @input="parseRawData"
          ></el-input>
        </el-card>

        <el-card class="stats-card" style="margin-top: 20px;">
          <div slot="header">
            <span>Êï∞ÊçÆÁªüËÆ°</span>
          </div>
          
          <div class="stats-grid">
            <div class="stat-item">
              <div class="stat-number">{{ csvData.length }}</div>
              <div class="stat-label">Êï∞ÊçÆË°å</div>
            </div>
            <div class="stat-item">
              <div class="stat-number">{{ columns.length }}</div>
              <div class="stat-label">ÂàóÊï∞</div>
            </div>
            <div class="stat-item">
              <div class="stat-number">{{ getTotalCells() }}</div>
              <div class="stat-label">ÊÄªÂçïÂÖÉÊ†º</div>
            </div>
            <div class="stat-item">
              <div class="stat-number">{{ getEmptyCells() }}</div>
              <div class="stat-label">Á©∫ÂçïÂÖÉÊ†º</div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'CsvProcessorView',
  metaInfo: {
    title: 'CSVÂ§ÑÁêÜÂô® - CSVÊñá‰ª∂Â§ÑÁêÜÂ∑•ÂÖ∑',
    meta: [
      { name: 'description', content: 'CSVÊñá‰ª∂ÁöÑËß£Êûê„ÄÅÁºñËæë„ÄÅËΩ¨Êç¢ÂíåÂØºÂá∫Â∑•ÂÖ∑ÔºåÊîØÊåÅÂ§öÁßçÂàÜÈöîÁ¨¶ÂíåÊ†ºÂºèËÆæÁΩÆ„ÄÇ' },
      { name: 'keywords', content: 'CSVÂ§ÑÁêÜ,CSVÁºñËæëÂô®,CSVËΩ¨Êç¢,Êï∞ÊçÆÂ§ÑÁêÜ,Ë°®Ê†ºÁºñËæë' }
    ]
  },
  data() {
    return {
      rawData: '',
      csvData: [],
      filteredData: [],
      columns: [],
      searchText: '',
      editingCell: {},
      editingHeader: -1,
      csvSettings: {
        delimiter: ',',
        quote: '"',
        hasHeader: true,
        skipEmptyLines: true
      }
    }
  },
  mounted() {
    this.loadSample()
  },
  methods: {
    handleFileUpload(file) {
      const reader = new FileReader()
      reader.onload = (e) => {
        this.rawData = e.target.result
        this.parseRawData()
      }
      reader.readAsText(file)
      return false // ÈòªÊ≠¢Ëá™Âä®‰∏ä‰º†
    },

    parseRawData() {
      if (!this.rawData.trim()) {
        this.csvData = []
        this.filteredData = []
        this.columns = []
        return
      }

      try {
        const lines = this.rawData.trim().split('\n')
        const parsedData = []
        
        for (let line of lines) {
          if (this.csvSettings.skipEmptyLines && !line.trim()) {
            continue
          }
          
          const row = this.parseCsvLine(line)
          parsedData.push(row)
        }

        if (parsedData.length === 0) {
          this.csvData = []
          this.filteredData = []
          this.columns = []
          return
        }

        // Á°ÆÂÆöÂàóÊï∞
        const maxColumns = Math.max(...parsedData.map(row => row.length))
        
        // ÁîüÊàêÂàóÈÖçÁΩÆ
        this.columns = []
        for (let i = 0; i < maxColumns; i++) {
          let label = `Âàó${i + 1}`
          if (this.csvSettings.hasHeader && parsedData.length > 0) {
            label = parsedData[0][i] || label
          }
          
          this.columns.push({
            prop: `col${i}`,
            label: label,
            width: 120
          })
        }

        // ËΩ¨Êç¢Êï∞ÊçÆÊ†ºÂºè
        const startIndex = this.csvSettings.hasHeader ? 1 : 0
        this.csvData = []
        
        for (let i = startIndex; i < parsedData.length; i++) {
          const row = {}
          for (let j = 0; j < maxColumns; j++) {
            row[`col${j}`] = parsedData[i][j] || ''
          }
          this.csvData.push(row)
        }

        this.filteredData = [...this.csvData]
        this.handleSearch()
      } catch (error) {
        this.$message.error('CSVËß£ÊûêÂ§±Ë¥•: ' + error.message)
      }
    },

    parseCsvLine(line) {
      const result = []
      let current = ''
      let inQuotes = false
      let i = 0

      while (i < line.length) {
        const char = line[i]
        
        if (char === this.csvSettings.quote && this.csvSettings.quote) {
          if (inQuotes && line[i + 1] === this.csvSettings.quote) {
            // ËΩ¨‰πâÁöÑÂºïÂè∑
            current += this.csvSettings.quote
            i += 2
          } else {
            // ÂºÄÂßãÊàñÁªìÊùüÂºïÂè∑
            inQuotes = !inQuotes
            i++
          }
        } else if (char === this.csvSettings.delimiter && !inQuotes) {
          result.push(current.trim())
          current = ''
          i++
        } else {
          current += char
          i++
        }
      }
      
      result.push(current.trim())
      return result
    },

    handleSearch() {
      if (!this.searchText.trim()) {
        this.filteredData = [...this.csvData]
        return
      }

      const searchLower = this.searchText.toLowerCase()
      this.filteredData = this.csvData.filter(row => {
        return Object.values(row).some(value => 
          String(value).toLowerCase().includes(searchLower)
        )
      })
    },

    handleCellClick(row, column) {
      const colIndex = this.columns.findIndex(col => col.prop === column.property)
      const rowIndex = this.filteredData.indexOf(row)
      
      if (colIndex >= 0 && rowIndex >= 0) {
        this.editingCell = { row: rowIndex, col: colIndex }
        this.$nextTick(() => {
          if (this.$refs.cellInput && this.$refs.cellInput[0]) {
            this.$refs.cellInput[0].focus()
          }
        })
      }
    },

    editHeader(index) {
      this.editingHeader = index
      this.$nextTick(() => {
        if (this.$refs.headerInput && this.$refs.headerInput[0]) {
          this.$refs.headerInput[0].focus()
        }
      })
    },

    addRow() {
      const newRow = {}
      this.columns.forEach(col => {
        newRow[col.prop] = ''
      })
      this.csvData.push(newRow)
      this.handleSearch()
    },

    addColumn() {
      const newIndex = this.columns.length
      const newColumn = {
        prop: `col${newIndex}`,
        label: `Âàó${newIndex + 1}`,
        width: 120
      }
      
      this.columns.push(newColumn)
      
      // ‰∏∫ÊâÄÊúâË°åÊ∑ªÂä†Êñ∞Âàó
      this.csvData.forEach(row => {
        row[newColumn.prop] = ''
      })
      
      this.handleSearch()
    },

    deleteRow(index) {
      const actualIndex = this.csvData.indexOf(this.filteredData[index])
      if (actualIndex >= 0) {
        this.csvData.splice(actualIndex, 1)
        this.handleSearch()
      }
    },

    deleteColumn(index) {
      if (this.columns.length <= 1) {
        this.$message.warning('Ëá≥Â∞ëÈúÄË¶Å‰øùÁïô‰∏ÄÂàó')
        return
      }

      const propToDelete = this.columns[index].prop
      this.columns.splice(index, 1)
      
      // ‰ªéÊâÄÊúâË°å‰∏≠Âà†Èô§ËØ•Âàó
      this.csvData.forEach(row => {
        delete row[propToDelete]
      })
      
      this.handleSearch()
    },

    loadSample() {
      this.rawData = `ÂßìÂêç,Âπ¥ÈæÑ,ÂüéÂ∏Ç,ËÅå‰∏ö,Ëñ™ËµÑ
Âº†‰∏â,28,Âåó‰∫¨,ËΩØ‰ª∂Â∑•Á®ãÂ∏à,15000
ÊùéÂõõ,32,‰∏äÊµ∑,‰∫ßÂìÅÁªèÁêÜ,18000
Áéã‰∫î,25,ÂπøÂ∑û,UIËÆæËÆ°Â∏à,12000
ËµµÂÖ≠,30,Ê∑±Âú≥,Êï∞ÊçÆÂàÜÊûêÂ∏à,16000
Èí±‰∏É,27,Êù≠Â∑û,ÂâçÁ´ØÂºÄÂèë,14000
Â≠ôÂÖ´,35,ÊàêÈÉΩ,È°πÁõÆÁªèÁêÜ,20000
Âë®‰πù,29,Ê≠¶Ê±â,ÂêéÁ´ØÂºÄÂèë,15500
Âê¥ÂçÅ,26,Ë•øÂÆâ,ÊµãËØïÂ∑•Á®ãÂ∏à,11000`
      this.parseRawData()
    },

    clearData() {
      this.rawData = ''
      this.csvData = []
      this.filteredData = []
      this.columns = []
      this.searchText = ''
    },

    downloadCsv() {
      if (this.csvData.length === 0) {
        this.$message.warning('Ê≤°ÊúâÊï∞ÊçÆÂèØ‰∏ãËΩΩ')
        return
      }

      let csvContent = ''
      
      // Ê∑ªÂä†Ê†áÈ¢òË°å
      if (this.csvSettings.hasHeader) {
        const headers = this.columns.map(col => this.escapeCsvValue(col.label))
        csvContent += headers.join(this.csvSettings.delimiter) + '\n'
      }
      
      // Ê∑ªÂä†Êï∞ÊçÆË°å
      this.csvData.forEach(row => {
        const values = this.columns.map(col => this.escapeCsvValue(row[col.prop] || ''))
        csvContent += values.join(this.csvSettings.delimiter) + '\n'
      })

      this.downloadFile(csvContent, 'data.csv', 'text/csv')
    },

    downloadJson() {
      if (this.csvData.length === 0) {
        this.$message.warning('Ê≤°ÊúâÊï∞ÊçÆÂèØ‰∏ãËΩΩ')
        return
      }

      const jsonData = this.csvData.map(row => {
        const obj = {}
        this.columns.forEach(col => {
          obj[col.label] = row[col.prop] || ''
        })
        return obj
      })

      const jsonContent = JSON.stringify(jsonData, null, 2)
      this.downloadFile(jsonContent, 'data.json', 'application/json')
    },

    escapeCsvValue(value) {
      const stringValue = String(value)
      if (stringValue.includes(this.csvSettings.delimiter) || 
          stringValue.includes('\n') || 
          stringValue.includes(this.csvSettings.quote)) {
        return this.csvSettings.quote + 
               stringValue.replace(new RegExp(this.csvSettings.quote, 'g'), 
                                 this.csvSettings.quote + this.csvSettings.quote) + 
               this.csvSettings.quote
      }
      return stringValue
    },

    downloadFile(content, filename, mimeType) {
      const blob = new Blob([content], { type: mimeType })
      const url = URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.href = url
      a.download = filename
      document.body.appendChild(a)
      a.click()
      document.body.removeChild(a)
      URL.revokeObjectURL(url)
      this.$message.success('Êñá‰ª∂‰∏ãËΩΩÊàêÂäü')
    },

    getTotalCells() {
      return this.csvData.length * this.columns.length
    },

    getEmptyCells() {
      let count = 0
      this.csvData.forEach(row => {
        this.columns.forEach(col => {
          if (!row[col.prop] || row[col.prop].toString().trim() === '') {
            count++
          }
        })
      })
      return count
    }
  }
}
</script>

